.slider__slick{
    justify-content: center;
    align-items: center;
    display: flex;
    box-shadow: var(--shadow);
}

.slick-slider {
    width: 50%;
    margin-top: 2rem;
    margin-bottom: 10rem;
}
.slide{
    padding: 10px;
    text-align: center;
    border-radius: 50px;

}

/* Slider Arrows */
.slick-prev,
.slick-next {
    background-color: #0a0a0a;
    /* Change the background color of the arrows */
    color: #000000;
    /* Change the color of the arrows */
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
}

.slide_link{
    font-size: 4rem;
    color: var(--primary-color);
}

.slide img{
    width: 100%;
    height: 100%;
    border-radius: 50px;
    padding: 20px;
    margin-top: 200px;
}
.github_button{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border-radius: 50px;
    background-color: var(--secondary-color);
    color: var(--primary-color);
    font-size: 1.5rem;
    margin-top: 20px;
    text-decoration: none;
    transition: 0.5s;
}
.github_button::after{
    position: absolute;
    content: "#";
    color: var(--primary-color);
    
}