.home{
    position: relative;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    align-items: center;
}
.intro{
    max-width: 540px;
    background-color: rgb(189, 170, 146);
    box-shadow: 0px 20px  20px 1px rgba(20, 40, 20, 10%);
}
.intro{
    position: relative;
    background-image: url(../assets/images/myPic.jpg),
    linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));


    background-size: cover;
    background-position: center-center;
    background-attachment: fixed;
    width: 100%;
    height: 100vh;
    border-radius: 20px;
    margin-top: 20px;
}
.intro::before{
    content: "";
    position: absolute;
    background: url(../assets/logo.svg);
    border-radius: 50%;
    top: 0;
    left: 0;
    width: 100px;
    height: 100px;
    transform: translate(-50%, -50%);
    z-index: 1;
}
.intro_child{
    
    width: 100%;
    font-size: 30px;
    border-radius: 20px;
    height: 100%;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2.5rem;
    color: aliceblue;
}
.home__name{
    color: aliceblue;
    font-weight: var(--font-bold);
    margin-bottom: 1.25rem;
}
.home__profession{
    font-size: var(--larg-font-size);
    color: var(--text-color-light);
    margin-bottom: 2.5rem;
}
.home__socials{
    display: flex;
    column-gap: 1.5rem;
    margin-bottom: 2.5rem;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}
.home__socials a{
    color: var(--text-color-light);
    font-size: 3.5rem;
    transition: 0.3s;
}
.home__socials a:hover{
    color: red;
    transform: scale(1.5);
}